.textInput {
  outline: none;
  border: none;
  font-size: 1.5rem;
  width: 90%;
}
.form_input1 {
  display: flex;
  width: 80%;
  height: max-content;

  border-radius: 2px;

  padding: 0.5rem 0.5rem 0.5rem 1rem;
  align-items: center;
  margin-top: 1.2rem;
  background-color: white;
}
.canvas {
  display: flex;
  justify-content: center;
  align-items: center;
  /* height: 100vh; */
  flex-direction: column;
}

.form {
  /* height: 80vh; */
  width: 70vw;
  background-color: #ffd8a9;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 6px;
  flex-direction: column;
  padding-top: 4rem;
  height: max-content;
  padding-bottom: 4rem;
}

.form_input {
  display: flex;
  width: 80%;
  height: max-content;

  border-radius: 2px;

  padding: 0.5rem 0.5rem 0.5rem 0.5rem;

  margin-top: 1.2rem;
  background-color: white;
}
.left {
  width: 1rem;
}
.content {
  width: 83%;
  padding-left: 0.5rem;
  text-align: justify;
}
.buttoncontrol1 {
  padding-left: 0.5rem;
}
.buttoncontrol {
  position: relative;
  padding-left: 0.5rem;
  padding-top: 1rem;
  /* width: 3rem; */
  /* float: right; */
  right: 0;
}
.tasks {
  width: 100%;
  top: auto;
  display: flex;
  justify-content: center;
  flex-direction: column;
  height: max-content;
  align-items: center;
}
.delete {
  padding-left: 1rem;
}
